<template>
  <div class="alerts">
    <v-container>
        <v-row dense> 
            <v-col cols="12" xl="5" lg="5" md="5" sm="5" xs="12">
               <Notifications /> 
            </v-col>
            <v-col cols="12" xl="5" lg="7" md="7" sm="7" xs="12">
              <v-card v-for="(device, key) in devices" :key="key" height="462px">
                <v-card-title class="text-center pt-1">ALARMS LIST </v-card-title>
                <perfect-scrollbar>
                <!-- <v-container class="mt-n9">
                  <v-layout row justify-space-between class="mb-n6 pt-2"> -->
                    <!-- <div class="box" :style="hover" v-for="(viewItem, key) in $displayOrder($view.alarm)" :key="key" >
                      <v-flex class=""  v-if="viewItem.style === 'warning'"> -->
                        <!-- <AlertBoxAll :id="viewItem._id" /> -->
                         <v-simple-table dense 
                           >
                            <template v-slot:default>
                              <thead>
                                <tr>
                                  <th class="text-left">
                                    Alarm
                                  </th>
                                  <th class="text-center">
                                    Type
                                  </th>
                                  <th class="text-center">
                                    Device
                                  </th>
                                  <th class="text-center">
                                    Create Event
                                  </th>
                                </tr>
                              </thead>
                              <tbody >
                                <tr
                                  v-for="(viewItem, key) in $displayOrder($view.alarm)" :key="key"
                                  
                                >
                                  <td class="text-left " >{{ viewItem.name }}</td>
                                  <td class="text-center">{{ alertType(viewItem._id) }}</td>
                                  <td class="text-center">{{ viewItem.device }}</td>
                                  <td class="text-center"><v-btn icon @click="addEvent(viewItem._id)"><v-icon>{{$icon.mdiAccessPoint}}</v-icon></v-btn></td>
                                </tr>
                              </tbody>
                            </template> 
  </v-simple-table>
                      <!-- </v-flex>
                    </div> -->
                    </perfect-scrollbar>
                 <!--  </v-layout>
                </v-container> -->
              </v-card>
            </v-col>
        </v-row>
        <v-row dense> 
            <v-col cols="12" xl="10" lg="12" md="12" sm="12" xs="12">
              <v-card>
                <v-card-title>ALARM LOGS</v-card-title>
                <!-- <v-card-subtitle>Coming soon...</v-card-subtitle> -->
                <!-- <v-container class="pt-0">
                  <v-row dense >
                    <v-col cols="12" sm="12" md="4" lg="2">
                        <v-select   :items="month" label="Device" outlined v-model="monthSelect" class="" ></v-select>
                    </v-col> 
                  </v-row>
                  <v-row dense >
                    <v-col cols="12" sm="12" md="4" lg="2" xl="1">
                        <v-card min-width="200px" outlined elevation="16">
                          <div class="d-flex justify-center">
                            <span class="body-1 mt-4">ALARM_01.CSV</span>
                          </div>
                          <v-card-actions><v-btn color="error" text>Delete</v-btn><v-btn color="primary" dark text>Download</v-btn></v-card-actions>
                        </v-card>
                    </v-col> 
                  </v-row> 
                </v-container> -->
                <v-container grid-list-xl>
                  <v-layout justify-center wrap>
                    <v-flex xs12 md8>
                      <v-card raised elevation="16 mb-10">
                        <v-card-title class="test white--text" >Download Files <v-spacer></v-spacer><v-spacer></v-spacer><v-select class="mb-n7" label="Device" v-model="deviceSelect" :items="alarmDevices" outlined @input="getAlarms()" ></v-select></v-card-title>
                        <br />
                        <v-container py-0>
                          <v-layout wrap v-if="showFiles">
                            <v-card-subtitle v-if="errorMsg">{{errorMsg}}</v-card-subtitle>
                            <v-flex v-for="(filesrc, index) in filesrcs" :key="index" xs12 md4>

                              <v-card outlined @click="download(filesrc)">
                                
                                <v-list>
                                  <v-list-item>
                                    <v-list-item-title>{{ filesrc }}</v-list-item-title>
                                    <v-list-item-icon>
                                      <v-icon class="primary--text">{{$icon.mdiDownload}}</v-icon>
                                    </v-list-item-icon>
                                  </v-list-item>
                                </v-list>
                              </v-card>
                            </v-flex>
                          </v-layout>
                          <v-layout wrap v-else>
                            <v-card-subtitle>Select a device to view available logs.</v-card-subtitle>
                            
                          </v-layout>
                        </v-container>
                      </v-card>
                    </v-flex>
                  </v-layout>
                </v-container>
                <!-- <v-card-text>Until this is completed, you can FTP directly to the NSITE touchscreens to view the Alarm logs.</v-card-text> -->
              </v-card>
            </v-col>
        </v-row>
    </v-container>
    <!-- <ReadingsDetails v-if="dialog" v-model="dialog" :id="activeID"/> -->
    <EventsEditDialog v-model="eventDialog" v-if="eventDialog"  :mode='"create"' :events="emptyEventWithID" />
  </div>
</template>

<script>
import Notifications from '@/components/Notifications.vue'
//import AlertBoxAll from '@/components/AlertBoxAll.vue'
import EventsEditDialog from '@/components/EventsEditDialog.vue'
//import {mdiAccessPoint} from '@mdi/js'

import eventBus from '../eventBus'
import address from '../server'

export default {
    name: 'AlertsVIew',
    components: {
        Notifications,
      //  AlertBoxAll,
        EventsEditDialog
    },
    data: () => ({
      errorMsg:'',
       deviceSelect:'',
       showFiles:false,
        alarmDevices:[],
        filesrcs:[],
        eventDialog:false,
        dialog:false,
        activeID:'',
        devices:['XL7'],
        items:['critical','warning'],
        typeSelect:'warning',
        viewAlarms:{},
        header:[
          {
            text:'Alert',
            align:'start',
            sortable: false,
            value: 'name' 
          },{
            text:'Type',
            align:'center',
            sortable: false,
            value: 'name' 
          },{
            text:'Device',
            align:'center',
            sortable: false,
            value: 'name' 
          },{
            text:'Create Event',
            align:'center',
            sortable: false,
            value: 'name' 
          },
        ]
    }),
    created(){
      eventBus.$on('details',this.openDetailsDialog)
      this.viewAlarms = this.$view.alarm
      Object.keys(this.viewAlarms).forEach(x => {
        if(!this.alarmDevices.includes(this.viewAlarms[x].device))this.alarmDevices.push(this.viewAlarms[x].device)
      })
    },
    mounted (){
        window.scrollTo(0, 0)
    },
    methods:{
      download(x){
        window.open(`http://${address}/api/ftp/get/alarms/XL7/ALARMS/${x}`)
        /* this.$http.get(`/api/ftp/get/alarms/XL7/ALARMS/${x}`,function(err,ret){
          console.log(ret.data.results)
        }) */
      },
      getAlarms(){
        if(this.deviceSelect)this.showFiles=true
        //let vm = this
        /* this.$http.get(`/api/ftp/list/alarms/${this.deviceSelect}/ALARMS`,function(err,ret){
          vm.filesrcs = []
          if(ret.data.status !== 'error'){
            vm.errorMsg =''
            ret.data.results.forEach(x => {
                vm.filesrcs.push(x)
            })
          } else {
            vm.errorMsg = 'Error connecting to this device or it may not support alarm logging.'
          }
      }) */
      this.$http.get(`/api/ftp/list/alarms/${this.deviceSelect}/ALARMS`).then(res => {
        this.filesrcs = []
        if(res.status !== 'error'){
            this.errorMsg =''
            res.results.forEach(x => {
                this.filesrcs.push(x)
            })
          } else {
            this.errorMsg = 'Error connecting to this device or it may not support alarm logging.'
          }
      })
      },
      openDetailsDialog(id){
        this.activeID = id
        this.dialog = !this.dialog 
      },
      testagain(x){
        console.log(x)
      },
      alertType(id){
          if(this.$view.alarm[id].style === 'error'){
              return 'Critical'
          } else if(this.$view.alarm[id].style === 'warning'){
              return 'Warning'
          } else {
              return ''
          }
      },
      addEvent(x){
        this.emptyEventWithID = this.$clone(this.$emptyEvent)
        this.emptyEventWithID.register_id = x
        this.emptyEventWithID.alias = this.$root.readingsTest[x].view.alias
        this.eventDialog = !this.eventDialog
      }
    },
}
</script>

<style scoped>
 /* .container {
    width: 100%;
    padding: 0px; 
  } 
  .dashboard {
    overflow: hidden;
  } */
  .ps{
  height: 400px;
}
.alerts {
    overflow: hidden;
  }
.test{
  background-color: #272727;
}
</style>