<template>
    <v-alert :type=" $vuetify.breakpoint.lg ? '' : $view.alarm[id].style" :color=" $vuetify.breakpoint.lg ? $view.alarm[id].style : ''"  border="left" class="mb-2"  text dense prominent dark v-if="show" elevation="6" @input="test">
        <v-row><v-col class="py-0"><div class="d-flex"><v-spacer></v-spacer><span class="caption">{{alertType}} - {{$view.alarm[id].device}}</span></div></v-col></v-row>
        <v-row><v-col class="pt-0 pb-2"><div class="d-flex justify-start"><span :class=" $vuetify.breakpoint.lg ? 'caption' : 'body-2'">{{$view.alarm[id].value}}</span></div></v-col></v-row>
    </v-alert>
</template>

<script>
//import mqttEvent from '../mqttEvent'
//import eventBus from "../eventBus"

export default {
    name:'AlertBox',
    props:['id'],
    data: () => ({}),
    created(){
        //console.log(this.id)
        this.$emit('alertFalse','test')
    },
    methods:{
        test(){
            //console.log(this.id," just closed")
           // mqttEvent.$emit('pub',this.id+" Just closed")
        }
    },
    computed:{
        show(){
            if(this.$view.alarm[this.id].value === '- - -' || this.$view.alarm[this.id].value === '--' || this.$view.alarm[this.id].value == 0){
                //eventBus.$emit('alertFalse',"subtract")
                return false
            } else {
                //eventBus.$emit('alertFalse','add')
                return true
            }
        },
        alertType(){
            if(this.$view.alarm[this.id].style === 'error'){
                return 'Critical'
            } else if(this.$view.alarm[this.id].style === 'warning'){
                return 'Warning'
            } else {
                return ''
            }
        }
    },

}
</script>

